import React from 'react'
import './Sidebar.css'
import Sidebarrow from './Sidebarrow'
import HomeIcon from '@mui/icons-material/Home'
import SubscriptionsIcon from '@mui/icons-material/Subscriptions'
import WhatshotIcon from '@mui/icons-material/Whatshot'
import SlowMotionVideoIcon from '@mui/icons-material/SlowMotionVideo'
import HistoryIcon from '@mui/icons-material/History'
import VideoLibraryIcon from '@mui/icons-material/VideoLibrary'
import WatchLaterIcon from '@mui/icons-material/WatchLater'
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown'
import LogoutIcon from '@mui/icons-material/Logout';
function Sidebar({isDisplay}) {
  
  return (
    <div className={isDisplay?"side_page open":"side_page"}>
      <div className='icons'>
        <Sidebarrow selected Icon={HomeIcon} title="Home" />
        <Sidebarrow Icon={SubscriptionsIcon} title="Subscription" />
        <Sidebarrow Icon={WhatshotIcon} title="Trending" />
        <Sidebarrow Icon={SlowMotionVideoIcon} title="Shorts" />
      </div>

      <hr />
      <div className='icons'>
        <Sidebarrow Icon={HistoryIcon} title="History" />
        <Sidebarrow Icon={VideoLibraryIcon} title="Library" />
        <Sidebarrow Icon={WatchLaterIcon} title="Watch Later" />
        <Sidebarrow Icon={ThumbUpOffAltIcon} title="Liked Video" />
        <Sidebarrow Icon={KeyboardArrowDownIcon} title="Show more" />
        <Sidebarrow Icon={LogoutIcon}  title="Log Out"/>
      </div>
      <hr/>
    </div>
  )
}

export default Sidebar
