import React, { useEffect, useState } from 'react'
import Header from './Header.jsx'
import Sidebar from './Sidebar.jsx'
import Searchpage from './Searchpage.jsx'
import Login from './login.jsx'
import './App.css'
import { Switch, Route, useHistory } from 'react-router-dom'

import VideoRecommend from './VideoRecommend.jsx'
import { useSelector } from 'react-redux'
import WatchScreen from './watchscreen/WatchScreen.jsx'
function App() {
  const [sideBar, toggleSidebar] = useState(false)
  function handleToggle() {
    toggleSidebar((value) => !value)
  }
  const history = useHistory()
  const { accessToken, loading } = useSelector((state) => state.auth)
  useEffect(() => {
    if (!accessToken && !loading) {
      history.push('/auth')
    }
  }, [accessToken, loading, history])
  return (
    <div>
      <Header isClicked={handleToggle} />
      <Switch>
        <Route path="/search" exact>
          <div className="search_view">
            <Sidebar isDisplay={sideBar} />
            <Searchpage />
          </div>
        </Route>
        <Route path="/auth" exact>
          <div className="login_page">
            <Login />
          </div>
        </Route>
        <Route path="/" exact>
          <div className="app_page">
            <Sidebar isDisplay={sideBar} />
            <VideoRecommend className="recommend" />
          </div>
        </Route>
        <Route path="/watch/:id" exact>
          <WatchScreen/>
        </Route>
      </Switch>
    </div>
  )
}
export default App
