import React, { useEffect, useState} from "react";
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import MicIcon from '@mui/icons-material/Mic';
import VideoCallIcon from '@mui/icons-material/VideoCall';
import NotificationsIcon from '@mui/icons-material/Notifications';
import YouTubeIcon from '@mui/icons-material/YouTube';
import {Link} from "react-router-dom"
import { useSelector } from "react-redux";
import "./Header.css"


function Header({isClicked}){
    const [inputSearch, setInput] = useState("")
    const [image, changeImg] = useState("https://static.vecteezy.com/system/resources/thumbnails/009/734/564/small/default-avatar-profile-icon-of-social-media-user-vector.jpg");

    const accessToken = useSelector(state => state.auth.user)
    useEffect( ()=> {
      if(accessToken)
      {
         changeImg(accessToken.photoURL)
      }
      else 
      {
         changeImg("https://static.vecteezy.com/system/resources/thumbnails/009/734/564/small/default-avatar-profile-icon-of-social-media-user-vector.jpg")
      }
    }, [accessToken, changeImg,image])
   return (
     
       <div className="header">
        <div className="header_left">
        <MenuIcon onClick={()=>isClicked()}/>
        <Link to="/" className="link_items">
        <YouTubeIcon className="header__logo" sx={{fontSize:38}}/>
        <p className="brand">Mytube</p>
           </Link>
     </div>
     <div className="header__mid">
        <div className="header_input"> 
        <input onChange={event => {
         setInput(event.target.value)
        }} value={inputSearch} placeholder="Search" type="text"></input>
        <Link to={`search/${inputSearch}`} className="link_items">
        <div className="header_inputButton">
         <SearchIcon />
        </div>
        </Link>
        </div>
        <MicIcon className="micIcon" />
     </div>
       <div className="header__right">
        <VideoCallIcon className="right_icon"/>
        <NotificationsIcon className="right_icon"/>
        <img alt="profile" src={image} className="right_icon"/>
       </div>
        
       </div>
       
    )
 }

  export default Header;