import { LOAD_PROFILE, LOGIN_FAILURE, LOGIN_REQUEST, LOGIN_SUCCESS, LOG_OUT } from "./ActionType";

const initialState = {
    accessToken:sessionStorage.getItem("mt-access-token")?sessionStorage.getItem("mt-access-token"): null,
    user: sessionStorage.getItem("mt-user")?JSON.parse(sessionStorage.getItem("mt-user")):null,
    loading:false
}

export const authReducer = (prevState = initialState, action) =>
{
   const {type, payload} = action;
    
   switch(type)
   {
    case LOGIN_REQUEST:
        return (
            {
                ...prevState,
                loading: true
            }
        )
    
        case LOGIN_SUCCESS:
            return {
                ...prevState,
                accessToken: payload,
                loading: false
            }

        case LOGIN_FAILURE:
            return {
                ...prevState,
                accessToken: null,
                loading: false,
                error: payload
            }

            case LOAD_PROFILE:
            return {
                ...prevState,
                user: payload
            }
            case LOG_OUT:
                 return{
                    ...prevState,
                    accessToken: null,
                    user: null,
                    loading: false
                 }
        default:
            return prevState
   }
}